export default {"ts":"import { Attributes, Component } from 'jinge';\n\nimport _tpl from './push.html';\n\nexport default class ExampleDrawerTemporary extends Component {\n  static template = _tpl;\n\n  showNavigation: boolean;\n  showSidepanel: boolean;\n\n  constructor(attrs: Attributes) {\n    super(attrs);\n    this.showNavigation = false;\n    this.showSidepanel = false;\n  }\n}\n","sass":":global {\n  :local(.demo) {\n    margin: 0 30px;\n    border: 1px solid rgb(0 0 0 / 12%);\n\n    .md-content {\n      padding: 16px;\n    }\n\n    &.md-active .md-content {\n      border-left: 1px solid rgb(0 0 0 / 12%);\n    }\n  }\n}\n","html":"<!-- import style from './push.scss' -->\n<md-drawer-container\n  pushMode\n  :class=\"['md-elevation-24', style.demo]\"\n  e:active=\"showNavigation\"\n  on:update.active=\"showNavigation = args[0]\"\n>\n  <_slot slot-pass:drawer>\n    <md-toolbar class=\"md-transparent\" elevation=\"0\">\n      <span class=\"md-title\">My App name</span>\n      <div class=\"md-toolbar-section-end\">\n        <md-button class=\"md-icon-button md-dense\" on:click=\"showNavigation = false\">\n          <md-icon-keyboard_arrow_left/>\n        </md-button>\n      </div>\n    </md-toolbar>\n\n    <md-list>\n      <md-list-item>\n        <md-icon-move_to_inbox/>\n        <span class=\"md-list-item-text\">Inbox</span>\n      </md-list-item>\n\n      <md-list-item>\n        <md-icon-send/>\n        <span class=\"md-list-item-text\">Sent Mail</span>\n      </md-list-item>\n\n      <md-list-item>\n        <md-icon-delete/>\n        <span class=\"md-list-item-text\">Trash</span>\n      </md-list-item>\n\n      <md-list-item>\n        <md-icon-error/>\n        <span class=\"md-list-item-text\">Spam</span>\n      </md-list-item>\n    </md-list>\n  </_slot>\n\n  <_slot slot-pass:main>\n    <md-toolbar class=\"md-accent\">\n      <if e:expect=\"!showNavigation\">\n      <md-button class=\"md-icon-button md-on-accent\" on:click=\"showNavigation = true\">\n        <md-icon-menu/>\n      </md-button>\n      </if>\n      <span class=\"md-title\">My Title</span>\n    </md-toolbar>\n    <md-content>\n      Lorem ipsum dolor sit amet, <br/>consectetur adipisicing elit. Error quibusdam,<br/> non molestias et! Earum magnam, similique,<br/> quo recusandae placeat dicta asperiores modi sint ea repudiandae maxime?<br/><br/><br/><br/> Quae non explicabo, neque.\n    </md-content>\n  </_slot>\n</md-drawer-container>"}