export default {"ts":"import { Attributes, Component } from 'jinge';\n\nimport _tpl from './over.html';\n\nexport default class ExampleDrawerTemporary extends Component {\n  static template = _tpl;\n\n  showNavigation: boolean;\n  showSidepanel: boolean;\n\n  constructor(attrs: Attributes) {\n    super(attrs);\n    this.showNavigation = false;\n    this.showSidepanel = false;\n  }\n}\n","sass":".demo {\n  min-height: 300px;\n  margin: 0 30px;\n  overflow: hidden;\n  position: relative;\n  border: 1px solid rgb(0 0 0 / 12%);\n\n  :global {\n    .md-drawer {\n      width: 230px;\n      max-width: calc(100vw - 125px);\n    }\n\n    .md-content {\n      padding: 16px;\n    }\n  }\n}\n","html":"<!-- import style from './over.scss' -->\n<div :class=\"['page-container md-elevation-24 md-layout-column', style.demo]\">\n  <md-toolbar class=\"md-accent\">\n    <md-button class=\"md-icon-button md-on-accent\" on:click=\"showNavigation = true\">\n      <md-icon-menu/>\n    </md-button>\n    <span class=\"md-title\">My Title</span>\n\n    <div class=\"md-toolbar-section-end\">\n      <md-button class=\"md-on-accent\" on:click=\"showSidepanel = true\">Favorites</md-button>\n    </div>\n  </md-toolbar>\n\n  <md-drawer e:active=\"showNavigation\" on:update.active=\"showNavigation = args[0]\">\n    <md-toolbar class=\"md-transparent\" elevation=\"0\">\n      <span class=\"md-title\">My App name</span>\n    </md-toolbar>\n\n    <md-list>\n      <md-list-item>\n        <md-icon-move_to_inbox/>\n        <span class=\"md-list-item-text\">Inbox</span>\n      </md-list-item>\n\n      <md-list-item>\n        <md-icon-send/>\n        <span class=\"md-list-item-text\">Sent Mail</span>\n      </md-list-item>\n\n      <md-list-item>\n        <md-icon-delete/>\n        <span class=\"md-list-item-text\">Trash</span>\n      </md-list-item>\n\n      <md-list-item>\n        <md-icon-error/>\n        <span class=\"md-list-item-text\">Spam</span>\n      </md-list-item>\n    </md-list>\n  </md-drawer>\n\n  <md-drawer right e:active=\"showSidepanel\" on:update.active=\"showSidepanel = args[0]\">\n    <md-toolbar class=\"md-transparent\" elevation=\"0\">\n      <span class=\"md-title\">Favorites</span>\n    </md-toolbar>\n\n    <md-list>\n      <md-list-item>\n        <span class=\"md-list-item-text\">Abbey Christansen</span>\n\n        <md-button class=\"md-icon-button md-list-action\">\n          <md-icon-chat_bubble class=\"md-primary\"/>\n        </md-button>\n      </md-list-item>\n\n      <md-list-item>\n        <span class=\"md-list-item-text\">Alex Nelson</span>\n\n        <md-button class=\"md-icon-button md-list-action\">\n          <md-icon-chat_bubble class=\"md-primary\"/>\n        </md-button>\n      </md-list-item>\n\n      <md-list-item>\n        <span class=\"md-list-item-text\">Mary Johnson</span>\n\n        <md-button class=\"md-icon-button md-list-action\">\n          <md-icon-chat_bubble/>\n        </md-button>\n      </md-list-item>\n    </md-list>\n  </md-drawer>\n\n  <md-content>\n    Lorem ipsum dolor sit amet, consectetur adipisicing elit. Error quibusdam, non molestias et! Earum magnam, similique, quo recusandae placeat dicta asperiores modi sint ea repudiandae maxime? Quae non explicabo, neque.\n  </md-content>\n</div>"}